$theme-primary: #BE1621;
$theme-dark: #222;

// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "mixins",
        "base"
;

.icon-custom-cloud {
    background-image: url("../img/chmura.svg");
    background-position: center center;
}

footer {
  height: 30px;
  width: 100%;
  position: relative;
}

.footer {
  margin: 0;
  position: absolute;
  width: 100%;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.svg {
  fill: $theme-primary
}